<template>
  <div class="page">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="账户设置"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content user-setting">
      <div class="user-info">
        <div>钱咖ID</div>
        <div>{{user_profiles.id}}</div>
        <div class="avatar">
          <div><img :src="user_profiles.avatar_url" alt=""></div>
          <div><img src="//qianka.b0.upaiyun.com/images/ad5f230271e58bbe32b0d7690b9f446a.png" alt=""></div>
          <input type="file" @change="updateAvatar($event.target)">
        </div>
      </div>
      <div class="info-input" @click="nickname()">
        <div class="title">昵称</div>
        <div class="default" v-if="!user_profiles.nickname">暂无昵称</div>
        <div class="default value" v-if="user_profiles.nickname">{{user_profiles.nickname}}</div>
        <img src="//qianka.b0.upaiyun.com/images/20ce1de8893719770f372f2465057bb0.png" alt="">
      </div>
      <div class="info-input" @click="bindMobile()">
        <div class="title">手机绑定</div>
        <div class="default" v-if="!user_profiles.mobile_number">暂未绑定</div>
        <div class="default value" v-if="user_profiles.mobile_number">{{mobileNumber}}</div>
        <img src="//qianka.b0.upaiyun.com/images/20ce1de8893719770f372f2465057bb0.png" alt="">
      </div>
      <div class="info-input" @click="bindWeixin()">
        <div class="title">微信绑定</div>
        <div class="default" v-if="!user_profiles.wx_bind">暂未绑定</div>
        <div class="default value" v-if="user_profiles.wx_bind">{{user_profiles.wx_nickname ? user_profiles.wx_nickname : '已绑定'}}</div>
        <img src="//qianka.b0.upaiyun.com/images/20ce1de8893719770f372f2465057bb0.png" alt="">
      </div>

      <div class="code" @click="qrcode()">
        <div class="title">查看二维码</div>
        <img src="//qianka.b0.upaiyun.com/images/6b7c32c778d51145910d1cad14ed8c2e.png" alt="">
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .user-setting {
    background: #FFFFFF;
    padding-top: 50px;
    padding-left: 30px;

    .user-info {
      position: relative;
      width: 100%;
      height: 89px;
      padding-top: 36px;
      margin-bottom: 30px;

      div:first-child {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      div:nth-child(2) {
        font-size: 24px;
        height: 33px;
        line-height: 33px;
        color: #B5B5B5;

        @include font-regular();
      }

      .avatar {
        div:nth-child(1) {
          position: absolute;
          top: 25px;
          right: 34px;

          img {
            width: 64px;
            height: 64px;
            border-radius: 50%;
          }
        }

        div:nth-child(2) {
          position: absolute;
          right: 30px;
          top: 69px;

          img {
            width: 24px;
            height: 24px;
          }
        }

        input {
          position: absolute;
          top: 25px;
          right: 34px;
          width: 64px;
          height: 64px;
          opacity: 0;
        }
      }
    }

    .info-input {
      margin-bottom: 30px;

      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;

        @include font-regular();
      }

      .default {
        display: inline-block;
        height: 33px;
        line-height: 33px;
        font-size: 24px;
        color: #DDDDDD;

        @include font-regular();

        &.value {
          color: #3a3a3a;
        }
      }

      img {
        margin-left: 3px;
        margin-bottom: 8px;
        vertical-align: bottom;
        width: 15px;
        height: 12px;
      }
    }

    .code {
      .title {
        font-size: 12px;
        color: #B5B5B5;
        line-height: 17px;
        height: 17px;
        margin-bottom: 6px;

        @include font-regular();
      }

      img {
        width: 30px;
        height: 30px;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapState, mapGetters, mapActions} from 'vuex'

  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_profiles'
      ]),

      ...mapGetters('user', [
        'mobileNumber'
      ])
    },

    mounted () {
      this.getProfile()
    },

    methods: {
      back () {
        $router.back('/user')
      },

      nickname () {
        $router.forward('/user/nickname')
      },

      bindMobile () {
        $router.forward('/user/bindMobile/' + this.user_profiles.mobile_bind)
      },

      bindWeixin () {
        if (this.user_profiles.wx_bind) {
          $router.forward('/user/unbindWeixin')
        } else {
          $router.forward('/user/bindWeixin')
        }
      },

      qrcode () {
        $router.forward('/user/qrcode')
      },

      ...mapActions('user', [
        'getProfile',
        'updateAvatar'
      ])
    }
  }
</script>




